import {IoArrowBack} from 'react-icons/io5'
import ReactPlayer from 'react-player'

const PlayerScreen = ({ recitersDetail, chaptersDetail }) => {

  const audioLink = (reciter, number) => {
    return reciter + '/' + ('00' + number).slice(-3) + '.mp3'
  }

  return (
    <div className="vh-100 rounded p-3 text-light player" id='player'>
        <h1 className="fs-5 fw-bold text-center mb-0"> Player </h1>
        <h2 className="fs-7 fw-bold text-center mt-0">  </h2>
        <hr />
        {recitersDetail !== null && chaptersDetail !== null ? (
        <ul className="list-group text-start w-lg-50 w-md-50 w-100 m-auto container">
          <div>
                <li className="list-group-item bg-transparent border-0 text-light py-0 cursor fs-5 ps-0 d-flex align-items-center justify-content-between">
                    <span>reciter / </span>
                    <span className="fs-6">{recitersDetail.name}</span>
                </li>
                <hr/>
                <li className="list-group-item bg-transparent border-0 text-light py-0 cursor fs-5 ps-0 d-flex align-items-center justify-content-between">
                    <span> </span>
                    <span className="fs-6">{chaptersDetail.name_arabic}</span>
                </li>
                <hr/>
                <li className="list-group-item bg-transparent border-0 text-light py-0 cursor fs-5 ps-0 d-flex align-items-center justify-content-between">
                    <span>Chapter name</span>
                    <span className="fs-6">{chaptersDetail.name_simple}</span>
                </li>
                <li className="list-group-item bg-transparent border-0 text-light py-0 cursor fs-5 ps-0 d-flex align-items-center justify-content-between">
                    <span>Chapter translated name</span>
                    <span className="fs-6">{chaptersDetail.translated_name.name}</span>
                </li>
                <hr/>
                <li className="list-group-item bg-transparent border-0 text-light py-0 cursor fs-5 ps-0 d-flex align-items-center justify-content-between">
                    <span>revelation place /  </span>
                    <div>
                    <span className="fs-6">{chaptersDetail.revelation_place}</span>
                    {chaptersDetail.revelation_place === 'madinah' ? (
                      <span className="fs-6"> / </span>
                      ): (
                      <span className="fs-6"> / </span>
                    )}
                    </div>
                </li>
                <hr/>

                <div>
                  <ReactPlayer url={
                    // audioLink(recitersDetail.Server, chaptersDetail.id)
                    audioLink(recitersDetail.Server, chaptersDetail.id)
                  } controls={true} playing={true} width='100%' height="60px" className="m-auto"/>
                </div>
          </div>
        </ul>
        ) : (
          <div className='text-center'>
              <span className='spinner-border text-light'></span>
              <br />
              <p>
                     
              </p>
          </div>
        )}
    </div>
  )
}

export default PlayerScreen