import { useState } from 'react';
import { IoMdCloseCircleOutline } from "react-icons/io";

const RecitersScreen = ({ reciters, reciterHandler }) => {
    const [activeId, setActiveId] = useState('');

    const RecitersScreenToggleFunction = () => {
        const RecitersScreen = document.querySelector('.RecitersScreen')
        RecitersScreen.classList.toggle('active')
      }

    return (
        <div className="RecitersScreen">
            <div className="container">
                <span className='fs-1 cursor text-light' onClick={RecitersScreenToggleFunction}>
                    <IoMdCloseCircleOutline/>
                </span>
            </div>
            <div className="vh-100 shadow-md rounded p-3 text-light container-lg">
                <h1 className="fs-5 fw-bold text-center mb-0"> Reciters </h1>
                <h2 className="fs-7 fw-bold text-center mt-0">  </h2>
                <hr />
                <div className='row justify-content-center gap-3 scroll'>
                {reciters && reciters.length > 0 ? (
                    reciters.map((reciter) => (
                        <div key={reciter.id} className={`list-group-item bg-transparent border-2 text-light text-end cursor rounded-3 fs-4 col-lg-2 col-md-4 col-sm-6 col-12 border-light p-0`}>
                            <div onClick={(e) => {
                                localStorage.setItem('SelectedReciter', reciter)
                                reciterHandler(reciter)
                                setActiveId(reciter.id)
                            }} className={`d-flex justify-content-center algin-items-center text-center w-100 px-3 py-3 ${reciter.id === activeId && 'active'}`}>
                                <span className="fs-6">{reciter.name}</span>
                            </div>
                        </div>
                    ))
                    ) : (
                        <div className='text-center'>
                            <span className='spinner-border'></span>
                        </div>
                    )
                }
                </div>
            </div>
        </div>
    )
}

export default RecitersScreen