import { useEffect, useState } from 'react';
import axios from 'axios';

import ChaptersScreen from './ChaptersScreen'
import PlayerScreen from './PlayerScreen'
import RecitersScreen from './RecitersScreen'

const Main = () => {
    const [reciters, setReciters] = useState([])
    const [chapters, setChapters] = useState([])
  
    const [recitersDetail, setRecitersDetail] = useState(null)
    const [chaptersDetail, setChaptersDetail] = useState(null)
  
    // get all reciters and audio
    useEffect(() => {
      async function fetchDate() {
        const {
          data: { reciters },
        } = await axios.get(`https://mp3quran.net/api/_arabic.php`)
        setReciters(reciters)
      }
      fetchDate()
    })
  
    // get all chapters
    useEffect(() => {
      async function fetchDate() {
        const { data: chapters } = await axios.get(`https://api.quran.com/api/v4/chapters`)
        setChapters(chapters)
      }
      reciters && reciters.length > 0 && fetchDate()
    }, [reciters])
  
    const reciterHandler = (reciter) => {
      setRecitersDetail(reciter)
    }
    const chaptersHandler = (chapter) => {
      setChaptersDetail(chapter)
    }

    // if (localStorage.getItem(''))

    return (
      <>
        <RecitersScreen reciters={reciters} reciterHandler={reciterHandler} />
        <ChaptersScreen chapters={chapters && chapters.chapters} chaptersHandler={chaptersHandler} />
        <PlayerScreen recitersDetail={recitersDetail} chaptersDetail={chaptersDetail} />
      </>
    )
}

export default Main