import { IoMdSettings } from "react-icons/io";
import { useState } from 'react';

const ChaptersScreen = ({ chapters, chaptersHandler }) => {

  const [activeId, setActiveId] = useState('')

  const RecitersScreenToggleFunction = () => {
    const RecitersScreen = document.querySelector('.RecitersScreen')
    RecitersScreen.classList.toggle('active')
  }

  function showPlayerNow() {
      const player = document.querySelector('.player')
      player.classList.add('active')
  }

  return (
    <div>
        <div className="container">
            <span className='fs-2 text-light w-100 cursor RecitersScreenToggle' onClick={RecitersScreenToggleFunction}>
                <IoMdSettings/>
            </span>
        </div>
        <div className="vh-100 shadow-md rounded p-3 container">
            <h1 className="text-light text-center">Quran -  </h1>
            <div className="d-flex justify-content-between text-light align-items-center">
                <span>
                    <h1 className="fs-1 position-relative fw-bold text-muted mb-0" style={{zIndex: '-1', top: '30px', opacity: '.5'}}> Chapters </h1>
                    <h2 className="fs-4 fw-bold mb-0"> Chapters </h2>
                </span>
                <span className="text-end">
                    <h1 className="fs-2 position-relative fw-bold text-muted mt-0" style={{zIndex: '-1', top: '30px', opacity: '.5'}}>  </h1>
                    <h2 className="fs-4 fw-bold mt-0">  </h2>
                </span>
            </div>
            <hr />
            <ul className="list-group text-end">
                <div className='row justify-content-center gap-3 flex-row-reverse' style={{height: '75vh', overflow: 'scroll'}}>
                {chapters && chapters.length > 0 ? (
                    chapters.map((chapter) => (
                            <a
                                href="#player"
                                key={chapter.id}
                                onClick={(e) => {
                                    showPlayerNow()
                                    chaptersHandler(chapter)
                                    setActiveId(chapter.id)
                                }}
                                className={`list-group-item bg-transparent border-2 text-light py-3 d-flex justify-content-end text-align-center cursor rounded-3 fs-4 col-lg-3 col-md-4 col-sm-6 col-12 border-light ${activeId === chapter.id && 'active'}`}>
                                <span className='px-2'>{chapter.name_arabic}</span>
                                <span className='bg-light py-0 px-2 rounded-3 border-0 bg-dark text-light'>{chapter.id}</span>
                            </a>
                    ))
                    ) : (
                    <div className='text-center'>
                        <span className='spinner-border text-light'></span>
                    </div>
                    )
                }
                </div>
            </ul>
        </div>
    </div>
    )
}

export default ChaptersScreen